'use strict';
let CSVParser = function () {
    if(!(this instanceof CSVParser)){
        throw new TypeError('CSVParser must created by new');
    }
    const parser = require('csvtojson');
    const Type = {
        Json:'json',
        CSV:'csv',
        Done:'done'
    };
    return {
        storeCsvToJson:function () {
            const csvFilePath='./csv/store/store_201706_01.csv';
            parser().fromFile(csvFilePath)
                .on(Type.Json,(obj)=>{
                    console.log(obj);
                })
                .on(Type.Done,(error)=>{
                    console.log(error)
                });

        }
    };
};
module.exports = CSVParser;